@import url("https://fonts.googleapis.com/css?family=Josefin+Sans");
body {
  font-family: "Josefin Sans", sans-serif;
  background: rgba(226, 239, 253, 0.5);
}
.sidebar {
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  font-family: "Josefin Sans", sans-serif;
  background: #fff;
  width: 250px;
  height: 100vh;
  position: fixed;
  top: 0px;
  left: 0;
  box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.08);
  z-index: 11;
}
.sidebar .nav-tabs {
  padding: 7px 10px;
}
.sidebar .user-info {
  position: relative;
  border-bottom: 1px solid #eee;
}
.sidebar .user-info .image img {
  width: 80px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
  vertical-align: bottom !important;
  border: 3px solid #fff;
  box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.2);
}
.sidebar .user-info .detail h4 {
  font-size: 20px;
}
.sidebar .user-info a {
  display: inline-block !important;
}
.sidebar .menu {
  position: relative;
}
.sidebar .menu .list {
  list-style: none;
  padding-left: 30px;
  margin-top: 42px;
}
.sidebar .menu .list li.active > :first-child span {
  font-weight: 700;
}
.sidebar .menu .list .header {
  font-size: 11px;
  font-weight: 700;
  padding: 8px 12px;
  position: relative;
  color: #455a64;
}
.sidebar .menu .list .header:before {
  content: "--";
  position: absolute;
  left: 0;
  top: 7px;
}
.sidebar .menu .list i.material-icons {
  margin-top: 4px;
}
.sidebar .menu .list .menu-toggle:after,
.sidebar .menu .list .menu-toggle:before {
  position: absolute;
  top: calc(50% - 11px);
  right: 17px;
  font-size: 15px;
  -moz-transform: scale(0);
  -ms-transform: scale(0);
  -o-transform: scale(0);
  -webkit-transform: scale(0);
  transform: scale(0);
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  font-family: "Material-Design-Iconic-Font";
}
.sidebar .menu .list .menu-toggle:before {
  /*! content:'\f2fb'; */
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  -webkit-transform: scale(1);
  transform: scale(1);
}
.sidebar .menu .list .menu-toggle:after {
  content: "\f2f9";
  -moz-transform: scale(0);
  -ms-transform: scale(0);
  -o-transform: scale(0);
  -webkit-transform: scale(0);
  transform: scale(0);
}
.sidebar .menu .list .menu-toggle.toggled:before {
  content: "\f2f9";
  -moz-transform: scale(0);
  -ms-transform: scale(0);
  -o-transform: scale(0);
  -webkit-transform: scale(0);
  transform: scale(0);
  font-family: "Material-Design-Iconic-Font";
}
.sidebar .menu .list .menu-toggle.toggled:after {
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  -webkit-transform: scale(1);
  transform: scale(1);
}
.sidebar .menu .list a {
  color: #2b3138;
  position: relative;
  padding: 14px 0px;
  font-size: 14px;
}
.sidebar .menu .list a:hover,
.sidebar .menu .list a:active,
.sidebar .menu .list a:focus {
  text-decoration: none !important;
}
.sidebar .menu .list a i {
  width: 16px;
  font-size: 16px;
}
.sidebar .menu .list a small {
  position: absolute;
  top: calc(50% - 7.5px);
  right: 15px;
}
.sidebar .menu .list a span {
  margin: 0 0 0 12px;
}
.sidebar .menu .list .ml-menu {
  list-style: none;
  display: none;
}
.sidebar .menu .list .ml-menu span {
  font-weight: normal;
  margin: 3px 0 1px 6px;
}
.sidebar .menu .list .ml-menu li a {
  padding-left: 40px;
  padding-top: 7px;
  padding-bottom: 7px;
}
.sidebar .menu .list .ml-menu li a:before {
  content: "\f30f";
  position: absolute;
  left: 14px;
  font-size: 12px;
  font-family: "Material-Design-Iconic-Font";
  color: #999;
  top: 8px;
}
.sidebar .menu .list .ml-menu li.active a.toggled:not(.menu-toggle) {
  font-weight: 600;
}
.sidebar .menu .progress-container {
  margin: 12px;
}
.list li {
  margin-bottom: 10px;
}

.navbar {
  z-index: 12;
}
.bg-light {
  background-color: #fff !important;
  box-shadow: 1px 0px 10px rgba(0, 0, 0, 0.08);
}
.loading {
  background-image: url("../img/loading.svg");
  background-repeat: no-repeat;
  height: 50px;
  width: 50px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.plugin-state-page {
  margin-left: 260px;
  margin-top: 30px;
}
input.form-control::placeholder {
  color: #6b666663;
  font-weight: 300;
}
@media (max-width: 450px) {
  .sidebar {
    width: 100px;
  }
  .sidebar .menu .list {
    list-style: none;
    padding-left: 10px;
    margin-top: 42px;
  }
  .sidebar .menu .list a {
    font-size: 12px;
  }
  .plugin-state-page {
    margin-left: 95px;
    margin-top: 30px;
  }
}
.widget {
  margin: 0 0 25px 0;
  display: block;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
}
.widget .widget-heading {
  padding: 7px 15px;
  -webkit-border-radius: 2px 2px 0 0;
  -moz-border-radius: 2px 2px 0 0;
  border-radius: 2px 2px 0 0;
  text-transform: uppercase;
  text-align: center;
  background: hsl(200, 8%, 30%);
  color: white;
}
.widget .widget-body {
  position: relative;
  padding: 20px 15px;
  font-size: 36px;
  font-weight: 300;
  background: #fdfdfd;
  min-height: 150px;
}
.widget-body.text-center {
  background: #fff;
  padding: 20px 0;
}
